import './topics-list.css';
import cards from './../../assets/cards';
import { Link } from 'react-router-dom';

const arr = ['Main Page',...cards[0]];

const TopicsList = ({index = '0'}) => {
  const list = arr.map((item, ind) => {
    let classLink = 'sidebar__link';
    if (ind.toString() === index) {
      classLink = 'sidebar__link sidebar__link_onClick'
    }
      return (
        <li className="sidebar__item" key={item.toString()}>
          <Link to={'/main/' + ind} className={classLink}>{item}</Link>
        </li>
      )
  })
  return (
    <ul className="sidebar__list">
      {list}
    </ul>
  )
}

export default TopicsList;
