import React from 'react';
import { BrowserRouter as Router, Route} from 'react-router-dom';
import ReactDOM from 'react-dom';
import Header from './header'
import Main from './main';

const App = ({train = true}) => {
  console.log('app', train)
  return(
  <Router>
    <div className="app">
      <Route path="/" exact> 
        <div>
          <Header index="0" train={train} />
          <Main train={train} />
        </div>
       </Route>
      {/* <Route path="/main" exact render={() => {
        return <Main index="0"/>
      }} /> */}
      <Route path="/main/:id" render={({match}) => {
        console.log('Route path=/main/:id',train);
        const id = match.params.id;
        return (
          <div>
            <Header index={id} train={train}/>
            <Main index={id} train={train} />
          </div>
        )
      }} />
    </div>
  </Router>
  )
}

export default App;