import React from 'react';
import './main.css';
import cards from './../../assets/cards';
import { Link } from 'react-router-dom';
import ReactDOM from 'react-dom';

let cardColor = 'card_train';

function reverse() {
  console.log('ee')
  el.classList.remove('flipped');
  el.removeEventListener('mouseleave', reverse);
}
let el: any;
function  getEvent (e: any) {
  if (e.target.className === 'card__button') {
    el = e.currentTarget.closest('.card-container');
    el.className += ' flipped';
    el.addEventListener('mouseleave', reverse);
  } else playSound(e.currentTarget.dataset.audio)
}
function playSound(addr: string) {
  const audio = new Audio(process.env.PUBLIC_URL + '/' + addr);
  audio.currentTime = 0;
  audio.play();
}
const getCategoryList = () => {
  console.log('getCategoryList',cardColor)
  return (
    cards[0].map((item, index) => {
      const arr = [...cards[index + 1]] as {word: string, translation: string, image: string, audioSrc: string}[];
      const addrImage = '/' + arr[0].image;
      return (
        <Link to={"/main/" + (index + 1)} className={'card ' + cardColor} key = {item.toString()}>
          <img src={addrImage} alt={arr[0].word} className="card__image_back"></img>
          {item}
        </Link>
      );
    })
  )
}
const getGameList = (i: string) => {
  const index = Number(i);
  return (
    cards[index].map((item) => {
      const arr = item as {word: string, translation: string, image: string, audioSrc: string};
      const addrImage = '/' + arr.image;
      return (
        <div className="card-container" key={arr.word}>
          <div className="card card_eng" data-audio={arr.audioSrc} onClick={getEvent}>
            <img src={process.env.PUBLIC_URL + addrImage} alt={arr.word} className="card__image_front"></img>
            {arr.word}
            <div className="card__button"></div>
          </div>
          <div className="card card_ru">
            <img src={process.env.PUBLIC_URL + addrImage} alt={arr.word} className="card__image_front"></img>
            {arr.translation}
          </div>
        </div>
      );
    })
  )
}
const Main = ({index = '0', train = true}) => {
  console.log('main', train)
  cardColor = train ? 'card_train' : 'card_game';
  const gameList = getGameList(index);
  const categoryList = getCategoryList();
  const list = index === '0' ? categoryList : gameList;
  return (
    <main className="main">
      {list}
    </main>
  )
  }

export default Main;
