import React from 'react';
import ReactDOM from 'react-dom';
import './header.css';
import Sidebar from './sidebar';
import App from './app';

interface IProps {
  index: string,
  train: boolean,
}

interface IState {
  sidebarHidden: boolean,
  trainOn: boolean,
}

class Header extends React.Component<IProps, IState> {
  constructor (props:any) {
    super(props);
    this.state = {
      sidebarHidden: true,
      trainOn: true,
    };
  }
  toggleSidebar = () => {
    this.setState((state) => ({
      sidebarHidden: !state.sidebarHidden,
    })
    );
  }
  toggleSwitch = () => {
    console.log('toggleSwitch this.state.trainOn',this.state.trainOn)
    this.setState((state) => ({
    trainOn: !state.trainOn,
    }));
    ReactDOM.render(<App train={!this.state.trainOn}/>, document.getElementById('root'));
  }

  render() {
    let classNameSidebar = 'sidebar';
    let classNameMenuToggle = 'menu-toggle';
    const sidebarHidden = this.state.sidebarHidden;
    if (sidebarHidden) {
      classNameSidebar += ' sidebar_hidden';
      classNameMenuToggle += ' menu-toggle_set-hidden';
    }
    let classNameSwitch = 'switch';
    let classNameSwitchHandle = 'switch__handle';
    let classNameSwitchText = 'switch__text';
    let switchText = 'train';
    if (this.state.trainOn !== this.props.train) {
      this.setState((state) => ({
        trainOn: !state.trainOn,
        }));
    }
    const trainOn = this.state.trainOn;
    console.log('trainOn',trainOn)
    if (!trainOn) {
      classNameSwitch += ' switch_game';
      classNameSwitchHandle += ' switch__handle_game';
      classNameSwitchText = 'switch__text_game';
      switchText = 'game';
      classNameSidebar += ' sidebar_game';
     }
    return (
      <header className="header" id="headerId">
        <Sidebar class={classNameSidebar} index={this.props.index} id="sidebarId" />
        <nav className={classNameMenuToggle} onClick={this.toggleSidebar}>
        </nav>
        <div className={classNameSwitch} onClick={this.toggleSwitch}>
          <div className={classNameSwitchHandle}></div>
          <div className={classNameSwitchText}>{switchText}</div>
        </div>
      </header>
    )
  }
}

export default Header;
